@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500&display=swap);
body{margin:0;font-family:"Roboto", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}button{border:none}ul{padding:0;list-style:none}@media (min-width: 800px){.body-layout{display:flex}}

.persons-list{margin:32px 0;max-height:160px;overflow:scroll;border:1px solid #f7f7f7}@media (min-width: 800px){.persons-list{max-height:400px}}.error{color:red;font-size:20px;text-align:center;margin:20px 0}.persons-list__person{padding:4px 8px;cursor:pointer}.persons-list__person:nth-child(even){background-color:#f7f7f7}.persons-list__person:hover{font-weight:bold}.persons-list__person.active{font-weight:bold}.actions{display:flex;justify-content:center}.actions__modify-list{margin:0 8px;border-radius:4px;text-transform:uppercase;padding:14px 16px 14px 16px !important;font-size:14px;line-height:20px;text-align:center;cursor:pointer;color:#4a4a4a;font-weight:400;background-color:white;box-shadow:0px 0px 2px rgba(55,71,79,0.2),0px 8px 12px rgba(0,0,0,0.1)}.persons-list-container{margin:8px 0}.persons-list__header{text-align:center;margin:32px 0}

.send{margin:32px 0;border-radius:4px;text-transform:uppercase;padding:14px 16px 14px 16px !important;font-size:14px;line-height:20px;text-align:center;color:#4a4a4a;font-weight:400;cursor:pointer;background-color:white;box-shadow:0px 0px 2px rgba(55,71,79,0.2),0px 8px 12px rgba(0,0,0,0.1);border:none}.edit-container{padding:8px}@media (min-width: 800px){.edit-container{padding:29px 0 0 32px}}.edit__img{border-radius:4px;box-shadow:0px 0px 2px rgba(55,71,79,0.2),0px 8px 12px rgba(0,0,0,0.1)}.input-container{margin:8px 0}.input__key{margin-right:8px}.input__value{box-shadow:0px 0px 2px rgba(55,71,79,0.2),0px 8px 12px rgba(0,0,0,0.1);border:none;border-radius:4px;padding:4px}.invalid{border:1px solid red}

.details-container{padding:8px}@media (min-width: 800px){.details-container{padding:29px 0 0 32px}}.edit{margin:32px 0;border-radius:4px;text-transform:uppercase;padding:14px 16px 14px 16px !important;font-size:14px;line-height:20px;text-align:center;color:#4a4a4a;font-weight:400;cursor:pointer;background-color:white;box-shadow:0px 0px 2px rgba(55,71,79,0.2),0px 8px 12px rgba(0,0,0,0.1)}.details__contact__header,.details__names__header{font-size:20px;border-bottom:1px solid #f7f7f7;margin:16px 0}.details__img{border-radius:4px;box-shadow:0px 0px 2px rgba(55,71,79,0.2),0px 8px 12px rgba(0,0,0,0.1)}.details__contact__phone,.details__contact__mail{display:flex;align-items:center}.details__contact__phone__icon,.details__contact__mail__icon{margin-right:8px}

.cursor-dot,.cursor-dot-outline{z-index:800;pointer-events:none;position:absolute;top:50%;left:50%;border-radius:50%;opacity:0;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);transition:opacity 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;transition:opacity 0.2s ease-in-out, transform 0.2s ease-in-out;transition:opacity 0.2s ease-in-out, transform 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out}.cursor-dot{width:8px;height:8px;background-color:#686768}.cursor-dot-outline{width:40px;height:40px;background-color:rgba(216,216,216,0.5)}

